import styled from 'styled-components';
import { Div, FixWidthContainer } from '../../theme/grid';
import { charcoal, themeColor, green } from '../../theme/variables';
import { media } from '../../theme/media';

export const Line = styled(FixWidthContainer)`
  font-size: 1.3em;  
  padding: 15px;
  
  ${media.tablet`
    font-size: 17px;
  `}
  
  a {
    font-size: 1.3em;  
    cursor: pointer;
    text-decoration: none;
    line-height: 1.3em;
    color: ${themeColor};
    position: relative;
    display: inline-block;
    ${media.tablet`
      font-size: 17px;
    `}
  }
  
  span {
    position: relative;
    display: inline-block;
    &:hover { 
     &:after {
      transform: scaleX(1);
      transition: transform 0.3s ease-out;   
    }  
  }
  }
  
  span:after {
    position: absolute;
    content: "";
    height: 2px;
    width: 100%;
    bottom: -5px;
    left: 0;
    background: ${green};
    transform-origin: 0%;
    transform: scaleX(0);
    transition: transform 0.3s ease-out;
  }
`;

export const ComponentContainer = styled(Div)`
  width: 100%; 
  text-align: center;
  padding-bottom: 15vh;
`;


